import { ReactComponent as Salary } from 'assets/icons/employeeDataContractWhite.svg'
import cn from 'classnames'
import AccordionComponent from 'common/Accordion'
import HistoryModal from 'common/History'
import Modal from 'common/Modal'
import Button from 'components/Button'
import TextInput from 'components/inputs/TextInput'
import { baseUrl } from 'constants/index'
import { format } from 'date-fns'
import { useFormik } from 'formik'
import useToggle from 'hooks/useToggle'
import { useState } from 'react'
import { useDropzone } from 'react-dropzone'
import { useDispatch, useSelector } from 'react-redux'
import api from 'services/api'
import notify from 'services/notify'
import {
  deleteNote,
  editComment,
  getEmployeeRecord,
} from 'store/ives/employees/actions'
import {
  Attachment,
  Employee,
  FileWithPreview,
  ReduxState,
  UserRole,
} from 'types'

import { FormSchema } from '..'
import styles from '../styles.module.scss'

interface EmployeeData {
  currentRecord: Employee
  allAccordions: boolean
}

const SalaryDataView = ({ currentRecord, allAccordions }: EmployeeData) => {
  const [slideSalary, setSlideSalary] = useState(false)
  const [currentEmployee, setCurrentEmployee] = useState<Employee>(
    currentRecord,
  )
  const currentProjectGroup = useSelector(({ user }: ReduxState) => user.user)
    ?.roles
  const [attachments, setAttachments] = useState<
    Attachment[] | { id: string; name: string }
  >([] as Attachment[])
  const dispatch = useDispatch()
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const toggleNotesModal = useToggle()
  const toggleSalaryModal = useToggle()
  const toggleAllowencesModal = useToggle()
  const IDquery = new URLSearchParams(window.location.search)
  const ID = IDquery.get('id')
  const [noteID, setNoteID] = useState<string | null>(null)
  const [commentSection, setCommentSection] = useState(false)
  const [addedDocuments, setAddedDocuments] = useState<FileWithPreview | any>()
  const { user } = useSelector(({ user }: ReduxState) => user)

  const Notes = useFormik({
    initialValues: {
      notes_main: '',
      notes_contract: '',
      notes_absence: '',
    },
    validationSchema: FormSchema,
    onSubmit: async (values) => {
      try {
        if (noteID) {
          dispatch(
            editComment(ID as string, {
              notes_contract: {
                id: noteID,
                attachments,
                note: values.notes_contract,
              },
            }),
          )
          setNoteID(null)
        } else {
          await api.post(`/employees?_format=json`, {
            id: ID,
            notes_contract: {
              attachments,
              note: values.notes_contract,
            },
          })
        }
        dispatch(
          getEmployeeRecord(ID as string, (record: Employee) =>
            setCurrentEmployee(record),
          ),
        )
        Notes.resetForm()
      } catch (error) {
        notify(error)
      }
    },
  })
  const isUTF8 = (string: string): boolean => {
    // eslint-disable-next-line no-plusplus
    for (let i = 0; i < string.length; i++) {
      if (string.charCodeAt(i) > 127) {
        return false
      }
    }
    return true
  }
  const { getRootProps, getInputProps, isDragActive, open } = useDropzone({
    accept: 'application/msword, text/plain, application/pdf',
    maxSize: 6000000,
    maxFiles: 1,
    onDrop: async (acceptedFiles) => {
      const file = acceptedFiles[0]
      if (isUTF8(file.name)) {
        if (file.size < 2097152) {
          const { data } = await api.post(
            '/file/upload/node/employee_note/field_attachments?_format=json',
            file,
            {
              headers: {
                'Content-Type': 'application/octet-stream',
                'Content-Disposition': `file; filename="${file.name.replace(
                  /^a-zA-Z0-9 ]/g,
                  '',
                )}"`,
              },
            },
          )
          setAddedDocuments(
            Object.assign(file, {
              // id: data.fid[0].value,
              preview: URL.createObjectURL(file),
            }),
          )
          setAttachments({ id: data.fid[0].value, name: file.name })
        } else {
          notify(
            'Die Größe der Datei sollte nicht mehr als 2Mb betragen.',
            'info',
          )
        }
      } else {
        notify(
          'Das aktuelle Dokument enthält ein oder mehrere Sonderzeichen in seinem Namen. Bitte benennen Sie das Dokument um und laden Sie es erneut hoch',
          'error',
        )
      }
    },
  })
  const handleEditComment = (id: any) => {
    setNoteID(id)
    toggleNotesModal.off()
    setCommentSection(true)
    Notes.setFieldValue(
      'notes_contract',
      currentEmployee?.notes_contract?.find(
        (note) => note.id.toString() === id?.toString(),
      )?.note,
    )
    const attachmentArray = currentEmployee?.notes_contract?.find(
      (note) => note.id.toString() === id?.toString(),
    )?.attachments
    const attachment = currentEmployee?.notes_contract
      ?.find((note) => note.id.toString() === id?.toString())
      ?.attachments.map((attachment) => attachment.name)
      .join('')
    setAddedDocuments({ id: 0, name: attachment })
    setAttachments(attachmentArray || [])
    // toggleNotesModal.off()\
  }
  const handleDeleteNote = async (id: number | string) => {
    toggleNotesModal.off()
    await dispatch(deleteNote(id as string))
    dispatch(
      getEmployeeRecord(ID as string, (record: Employee) =>
        setCurrentEmployee(record),
      ),
    )
  }
  const handleEditAttachment = (id: any) => {
    handleEditComment(id)
    open()
  }

  return (
    <>
      <div className={styles.detailMainWrap}>
        <div
          className={cn(
            styles.detailMain,
            styles.contract,
            slideSalary && styles.open,
          )}
        >
          <div className={cn(styles.detailHeader, styles.contract)}>
            <h2>{'Gehaltsdaten'}</h2>
          </div>
          <div className={cn(styles.detailMainInnerOverview, styles.contract)}>
            <div className={styles.detailBody}>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Gehalt'}</span>
                <br />
                <span className={styles.dataValue}>
                  {`${currentEmployee.salary || 0} €`}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Wochenstunden/VBE'}</span>
                <br />
                <span className={styles.dataValue}>
                  {currentEmployee.vbe || 'Keine Angabe'}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Letze Prämie (Summe + Datum)'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {currentEmployee.bonuses?.map((bonus, index) => (
                    <p key={index}>
                      {format(
                        new Date(bonus.substr(0, bonus.indexOf(' '))),
                        'dd.MM.yyyy',
                      )}
                      {' - '}
                      {bonus.substr(bonus.indexOf(' '), bonus.length - 1)}
                      {' € '}
                    </p>
                  )) || 'Keine Angabe'}
                </span>
              </div>
            </div>
          </div>
          <div className={styles.detailMainInner}>
            <div className={styles.historyWrap}>
              <div className={styles.historyHeader}>
                <h3 className={styles.fieldTitle}>{'Gehaltsentwicklung'}</h3>

                {currentProjectGroup?.includes(UserRole.HumanResource) &&
                  currentEmployee.financial_details && (
                    <Button
                      className={cn(styles.contract, styles.historyButton)}
                      variant="fulfilled"
                      onClick={toggleSalaryModal.on}
                    >
                      {'Historienansicht'}
                    </Button>
                  )}
              </div>
            </div>
            <AccordionComponent
              expanded={allAccordions}
              title="Detail Bonuszahlungen"
              currentTitleStyle={styles.contract}
            >
              {' '}
              <span className={styles.dataValue}>
                {currentEmployee?.allowences?.map((item, index) => (
                  <div className={styles.dataWrap} key={index}>
                    <span className={styles.dataLabel}>{item.type}</span>
                    <br />
                    <span className={styles.dataValue}>
                      {`${item.amount} € ${item.date}`}
                    </span>
                  </div>
                ))}
                <div className={styles.historyButton}>
                  {currentEmployee.allowences &&
                    currentEmployee.allowences?.length > 3 && (
                      <Button
                        className={cn(styles.contract, styles.historyButton)}
                        variant="fulfilled"
                        onClick={toggleAllowencesModal.on}
                      >
                        {'Historienansicht'}
                      </Button>
                    )}
                </div>
              </span>
            </AccordionComponent>
            <AccordionComponent
              expanded={allAccordions}
              title="Letzte Vertragsnotizen"
              currentTitleStyle={styles.contract}
            >
              <span className={styles.dataValue}>
                {currentProjectGroup?.includes(UserRole.HumanResource) &&
                  currentEmployee.notes_contract
                    ?.slice(-3)
                    .reverse()
                    .flatMap((note, index) => (
                      <div className={styles.dateIncludedField} key={index}>
                        <span className={styles.dateLabel}>
                          {note.created &&
                            note.created.substr(
                              note.created.indexOf(', ') + 1,
                              note.created.indexOf(' - ') - 3,
                            )}
                        </span>
                        <span className={styles.dataValue}>{note.note}</span>
                      </div>
                    ))}
              </span>
              <div className={styles.historyButton}>
                <Button
                  className={cn(styles.contract, styles.historyButton)}
                  variant="fulfilled"
                  onClick={toggleNotesModal.on}
                >
                  {'Historienansicht'}
                </Button>
              </div>
            </AccordionComponent>
            <AccordionComponent
              expanded={allAccordions || commentSection}
              title="Aktuelle Vertragsnotizen"
              currentTitleStyle={styles.contract}
            >
              <form onSubmit={Notes.handleSubmit}>
                <p className={styles.dataLabelAttachment}>
                  {addedDocuments && (
                    <a
                      href={`${baseUrl}/sites/default/files/employees/notes/${addedDocuments.name}`}
                      target="_blank"
                      className={styles.dataLabelAttachment}
                      rel="noreferrer"
                    >
                      {addedDocuments.name}
                    </a>
                  )}
                </p>
                <TextInput
                  name="notes_contract"
                  label={`${format(new Date(), 'dd.MM.yyyy')}`}
                  multiline
                  rows={5}
                  formik={Notes}
                  onChange={Notes.handleChange}
                  value={Notes.values.notes_contract}
                />
                <div className={styles.notesButtonWrap}>
                  <div
                    {...getRootProps({
                      className: cn(styles.dndContainer, {
                        [styles.active]: isDragActive,
                      }),
                    })}
                  >
                    <input
                      {...getInputProps()}
                      accept=".png,.jpg,.jpeg,.svg,.pdf"
                    />
                    <Button
                      variant="fulfilled"
                      className={cn(styles.contract, styles.upload)}
                    >
                      {'Hochladen'}
                    </Button>
                  </div>
                  <Button
                    variant="fulfilled"
                    color="orange"
                    className={styles.notesButton}
                    type="submit"
                  >
                    {'Notiz speichern'}
                  </Button>
                </div>
              </form>
            </AccordionComponent>
          </div>
        </div>
        <Button
          className={cn(
            styles.detailBtn,
            styles.contractBtn,
            slideSalary && styles.toggleContract,
          )}
          onClick={() => setSlideSalary(!slideSalary)}
        >
          {(!slideSalary && <Salary className={styles.contractIcon} />) || (
            <Salary />
          )}
          <h4 className={styles.sideTitle}>{'Gehaltsdaten'}</h4>
        </Button>
      </div>
      <Modal
        isOpen={toggleNotesModal.value}
        onRequestClose={toggleNotesModal.off}
        withCloseButton
        maxWidth={1000}
      >
        <HistoryModal
          modalType="notes"
          notesData={currentEmployee.notes_contract}
          handleEditComment={handleEditComment}
          handleEditAttachment={handleEditAttachment}
          handleDeleteNote={handleDeleteNote}
          isShowingDelete={
            user?.project_group.name ===
            'Controlling / Sekretariat / Administration'
          }
        />
      </Modal>
      <Modal
        isOpen={toggleSalaryModal.value}
        onRequestClose={toggleSalaryModal.off}
        withCloseButton
        maxWidth={1000}
      >
        <HistoryModal
          modalType="salary"
          salaryData={currentEmployee.financial_details}
        />
      </Modal>
      <Modal
        isOpen={toggleAllowencesModal.value}
        onRequestClose={toggleAllowencesModal.off}
        withCloseButton
        maxWidth={800}
      >
        <HistoryModal
          modalType="bonus"
          allowencesData={currentEmployee.allowences}
        />
      </Modal>
    </>
  )
}
export default SalaryDataView
